import { Component, OnInit } from '@angular/core';
import { SelectItem } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng'; // 
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ProductManageHttpService } from '../../http/product-manage.http.service';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { DisplaypropertiesBean } from './bean/display-properties.bean';

@Component({
  selector: 'app-display-properties',
  templateUrl: './display-properties.component.html',
  styleUrls: ['./display-properties.component.scss'],
  providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
export class DisplaypropertiesComponent implements OnInit {
  updateValue: any;
  tabMesg: any = tableMessage; // 
  // bean
  displaypropertiesBean: DisplaypropertiesBean = new DisplaypropertiesBean();
  // 
  // 
  cars_2: any;
  // 
  total: string;
  // 
  pageSize: string = '10';
  // 
  showModel: string;
  headerTitle: string
  display: boolean = false;
  // 
  msgs: Message[] = [];
  // 
  first: number = 0;
  statuslist: any = [];
  code: any;
  constructor(
    public productManageHttpService: ProductManageHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc) {
    this.codeValues(); // 
    this.statuslist = this.code['statusList'];
  }

  ngOnInit() {
    this.displaypropertiesBean.pageSize = 10;
    this.displaypropertiesBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  // 
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret; // code
	}

  // 
  resetClick() {
    this.displaypropertiesBean = new DisplaypropertiesBean();
  }

  // 
  querySecle() {
    this.displaypropertiesBean.pageSize = 10;
    this.displaypropertiesBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }

  // 
  queryClick() {
    this.productManageHttpService.queryProductAttrConfigList(this.displaypropertiesBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.cars_2 = data.resultList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    });
  }
  // 
  detailsClick(item) {
    this.display = true;
    this.headerTitle = '';
    this.showModel = '2';
    this.updateValue = item;
  }
  // 
  detailsCall(param) {
    this.display = param;
    // this.queryClick();
  }
  // 
  paginate(event) {
    // 
    this.displaypropertiesBean.pageSize = event.rows;
    // 
    let currentPage = event.page + 1;
    this.displaypropertiesBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.displaypropertiesBean.pageSize;
    // 
    this.queryClick();
    this.displaypropertiesBean.pageNum = 1;
  }
  // 
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

  isCompleteClick(item) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        // 
        let parems = {
          productAttrConfigId: item.productAttrConfigId,
          status: 'Y',
        };
        this.productManageHttpService.modifyProductAttrConfigStatus(parems).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: '' });
        });
      }
    });

  }
}
